import React from 'react';
import { BrowserRouter, Route } from 'react-router-dom';
import Login from '../Login';
import NavBar from '../Navbar';
import ShoppingCart from '../ShoppingCart'
import Form from '../Form';

export default class Root extends React.Component{
    componentDidMount(){
        this.props.FetchProducts.bind(null)();
    }
    
    render(){
        let navbar = <div></div>
        let startPage = <Route exact path='/' render={({history})=><Login history={history} {...this.props}/>}/>
        // console.log(this.props.login.isValidated)
        if(this.props.login.isValidated){
            navbar = <NavBar/>
            startPage = <Route exact path='/' render={({history})=>{history.replace('/shoppingcart')}}/>
        }
        return (
            <BrowserRouter>
              {/* {navbar} */}
              {/* {startPage} */}
              <Route exact path='/' render={({history})=><Login history={history} {...this.props}/>}/>
              <Route exact path='/shoppingcart' render={()=><ShoppingCart {...this.props}/>}/>
              <Route exact path='/newproduct' render={()=><Form {...this.props}/>}/>
            </BrowserRouter>
        );
    }
}