export function products(defaultStore=[], action){
    switch(action.type){
        case "ADD_NEW_PRODUCT": 
            // console.log(action);
            // return [...defaultStore, {...action.newProduct, id:defaultStore[defaultStore.length-1].id+1}];
            return [...defaultStore, action.newProduct];
        
        case "REMOVE_PRODUCT": 
            // console.log(defaultStore,action.theId);
            return defaultStore.filter(product=>{return product.id !== action.theId})

        case "INCREMENT_LIKES": 
            // console.log(defaultStore,action.theId);
            // return defaultStore.map(product=>{
            //     if(product.id === action.theId)
            //         product.likes++;
            //     return product
            // })
            console.log(action.index)
            return [...defaultStore.slice(0,action.index),
                    {...defaultStore[action.index],likes:defaultStore[action.index].likes+1},
                    ...defaultStore.slice(action.index+1)];
        
        case "FETCH_PRODUCTS":
            return action.response;

        default: return defaultStore;
    }
}