import axios from 'axios';
export function ValidateLogin(data){
    return (dispatch)=>{
            axios.post("http://localhost:5000/validatelogin",data).then(res=>{
                dispatch({type:'LOGIN_RESPONSE',res})
        })
    }
}
export function AddNewProduct(newProduct){
    axios.post("http://localhost:5000/addproduct",newProduct).then(res=>{
        console.log(res);
    })
    return {type:'ADD_NEW_PRODUCT',newProduct};
}
export function RemoveProduct(theId){
    axios.post("http://localhost:5000/deleteproduct",{theId}).then(res=>{
        console.log(res);
    })
    return {type:'REMOVE_PRODUCT',theId};
}
export function IncrementLikes(index,id){
    axios.post("http://localhost:5000/incrementlikes",{id}).then(res=>{
        console.log(res);
    })
    return {type:'INCREMENT_LIKES',index};
}
export function AddUser(){
    return {type:'ADD_USER'};
}
export function FetchProducts(){
    return (dispatch) => {
        // axios.get("http://api.myjson.com/bins/rwxws").then(res=>{
        //     dispatch({type:'FETCH_PRODUCTS', response:res.data})
        // })
        axios.get("http://localhost:5000/products").then(res=>{
            dispatch({type:'FETCH_PRODUCTS', response:res.data})
        })
    }
}