import React, {Component} from 'react';
import Navbar from '../Navbar';

export default class Form extends Component{
    createNewItem = () => {
        let products = this.props.products;
        let newProduct = {
            id: products[products.length-1].id+1,
            title:this.refs.name.value,
            rating:this.refs.rating.value,
            price:this.refs.price.value,
            ImageUrl:this.refs.image.value,
            likes:0,
            quantity:0
        }
        this.props.AddNewProduct.bind(this,newProduct)()
    }
    render(){
        return(
            <React.Fragment>
                <Navbar/>
                <div className="container">
                    <div>
                        <h1 className="jumbotron"> Welcome to online shopping! </h1>
                    </div>
                    <div className="col-xs-12 col-lg-6 alignCenter mt-5 col-lg-push-3">
                        <div className="inlineBlock">
                            <label>Add New Product</label>
                            <div className="form-group">
                                <label htmlFor="name">Name</label>
                                <input type="text" className="form-control"ref="name" placeholder="Product Name"/>
                            </div>
                            <div className="form-group">
                                <label htmlFor="rating">Rating</label>
                                <input type="number" className="form-control" ref="rating" placeholder="Rating" min="1" max="5"/>
                            </div>
                            <div className="form-group">
                                <label htmlFor="rating">Price</label>
                                <input type="number" className="form-control" ref="price" placeholder="Price" min="1"/>
                            </div>
                            <div className="form-group">
                                <label htmlFor="image">Image URL</label>
                                <input type="text" className="form-control" ref="image" placeholder="Image URL"/>
                            </div>
                            <button className="btn btn-primary" onClick={this.createNewItem.bind(this)}>Submit</button>
                        </div>
                    </div>
                </div>
            </React.Fragment>
        )
    }
}