import React from 'react';
import Product from '../Product';
import Loader from '../Loader';
import Navbar from '../Navbar';

export default class ShoppingCart extends React.Component{
    render(){
        let productList = <Loader/>;
        if(this.props.products.length){
            productList = this.props.products.map((item,index)=> <Product data={item} key={item.id} index={index} {...this.props}/>)
        }
        return(
            <React.Fragment>
                <Navbar/> 
                <div className="container">
                    <div>
                        <h1 className="jumbotron"> Welcome to online shopping! </h1>
                    </div>
                    <div className="row">
                        <div className="row col-xs-12 col-lg-12 mt-5 products">
                            {productList}
                        </div>
                    </div>
                </div>
            </React.Fragment>
        )
    }
}