export function login(defaultStore={isValidated:false},action){
    // return defaultStore; //return a new store!
    switch(action.type){
        case "LOGIN_RESPONSE": 
            // console.log(action.res)
            if(action.res.data === "success"){
                return {isValidated:true}
            }
            return defaultStore;
        default: return defaultStore;
    }
}