import React from 'react';
import './main.css';

export default class Login extends React.Component{
    handleFormSubmit = (e) => {
        e.preventDefault();
        
        //fetch the deatils from textboxes
        //send info to server
        //success then redirect!
        let username = this.refs.username.value;
        let password = this.refs.password.value;
        this.props.ValidateLogin.bind(null)({username,password})
        // let {history} = this.props;
        // history.push("./shoppingcart")
        // console.log(this.props);
    }
    componentDidUpdate(){
        if(this.props.login.isValidated){
            let {history} = this.props;
            history.replace("./shoppingcart");
            // history.push("./shoppingcart") 
        }
    }
    render(){
        return(
            <div className="body">
                <div className="container h-100">
                    <div className="d-flex justify-content-center h-100">
                        <div className="user_card">
                            <div className="d-flex justify-content-center">
                                <div className="brand_logo_container">
                                    <img src="https://cdn.freebiesupply.com/logos/large/2x/pinterest-circle-logo-png-transparent.png" className="brand_logo" alt="Logo"/>
                                </div>
                            </div>
                            <div className="d-flex justify-content-center form_container">
                                <form onSubmit={this.handleFormSubmit.bind(this)}>
                                    <div className="input-group mb-3">
                                        {/* <div className="input-group-append">
                                            <span className="input-group-text"><i className="fas fa-user"></i></span>
                                        </div> */}
                                        <input type="text" ref="username" className="form-control input_user" placeholder="username"/>
                                    </div>
                                    <div className="input-group mb-2">
                                        {/* <div className="input-group-append">
                                            <span className="input-group-text"><i className="fas fa-key"></i></span>
                                        </div> */}
                                        <input type="password" ref="password" className="form-control input_pass" placeholder="password"/>
                                    </div>
                                    {/* <div className="form-group">
                                        <div className="custom-control custom-checkbox">
                                            <input type="checkbox" className="custom-control-input" id="customControlInline"/>
                                            <label className="custom-control-label" htmlFor="customControlInline">Remember me</label>
                                        </div>
                                    </div> */}
                                    <div className="d-flex justify-content-center mt-3 login_container">
                                        <input type="submit" name="button" value="Login" className="btn login_btn"/>
                                    </div>
                                </form>
                            </div>
                            {/* <div className="mt-4">
                                <div className="d-flex justify-content-center links">
                                    Don't have an account? <a href="#" className="ml-2">Sign Up</a>
                                </div>
                                <div className="d-flex justify-content-center links">
                                    <a href="#">Forgot your password?</a>
                                </div>
                            </div> */}
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}