import React, {Component} from 'react';
import './main.css';
export default class Product extends Component{
    render(){
        return(
            <div className="col-xs-12 col-sm-6 col-xl-4 mb-5">
                <div className="card-header text-white bg-back noWrap">
                    <div className="glyphicon glyphicon-remove float-right bg-danger" onClick={this.props.RemoveProduct.bind(null,this.props.data.id)}></div>
                    {this.props.data.title}
                </div>
                <div className="card-body bg-light">
                    <div className="row">
                        <div className="col-xs-6 col-sm-6 col-md-6">
                            <img src={this.props.data.ImageUrl} className="cardImage" alt="..."/>
                        </div>
                        <div className="col-xs-6 col-sm-6 col-md-6">
                            <div className="card-body">
                                <h5 className="card-text noWrap">
                                    Price: {this.props.data.price}
                                </h5>
                                <h5 className="card-text noWrap">
                                    Rating: {this.props.data.rating}
                                </h5>
                                <h5 className="card-text noWrap">
                                    Quantity: {this.props.data.quantity}
                                </h5>
                                {/* <h5 className="card-text noWrap">
                                    Likes: {this.props.data.likes}
                                </h5> */}
                            </div>
                        </div>
                    </div>
                </div>
                <div className="card-footer bg-back">
                    <button className="btn btn-light" onClick={this.props.IncrementLikes.bind(null,this.props.index,this.props.data.id)}>
                        <span className="glyphicon glyphicon-thumbs-up"/>
                        {this.props.data.likes}
                    </button>
                    <button className="btn btn-light float-right">Add</button>
                </div>
          </div>
        )
    }
}